import { http } from "src/common/helper";
import serviceName from "src/common/helper/serviceName";
import Swal from "sweetalert2";
import { loadingStatus } from "./commonAction";


export const setFormKab = (formType, formValue) => {
  return { type: "ADD_KAB", formType, formValue };
};


export const GetKabupatens = (type, IdProvinsi) => {
  return (dispatch) => {
    loadingStatus(true);
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/GetKabupatens?IdProvinsi=${IdProvinsi}`
      )
      .then((res) => {
          loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          dispatch({ type, payload: data.data });
        } else {
          Swal.fire("Gagal", `${data.ReturnMessage}`, "error");
        }
      });
  };
};

export const GetKabupatenById = (idKabupaten) => {

  return(dispatch) =>{
    loadingStatus(true);
    dispatch({ type: "RESET_FORM_KAB" });
    http.get(
      `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/GetKabupaten?IdKabupaten=${idKabupaten}`
    ).then(res =>{
      loadingStatus(false);
      let dataKab = res.data;
      if (dataKab.isSuccess) {
        dispatch(setFormKab("IdKabupaten", idKabupaten));
        dispatch(setFormKab("IdProvinsi", dataKab.data.idProvinsi));
        dispatch(setFormKab("Kode", dataKab.data.kode));
        dispatch(setFormKab("Nama", dataKab.data.nama));
          dispatch({ type: "MODAL_KAB", payload: true });
      }else{
          Swal.fire("Gagal", `${dataKab.ReturnMessage}`, "error");
      }
    });
  }
}

export const PostKabupaten = (data) => {
  return(dispatch) => {
    loadingStatus(true);
    let param = `?IdProvinsi=${data.IdProvinsi}&Kode=${data.Kode}&Nama=${data.Nama}`;
    http
      .post(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/AddKabupaten${param}`
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          Swal.fire("Berhasil", `Anda telah berhasil menambah data`, "success");
          dispatch({ type: "MODAL_KAB", payload: false });
          dispatch({ type: "RESET_FORM_KAB" });
          GetKabupatens("GET_KABUPATENS", data.IdProvinsi);

        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });

  }
}

export const UpdateKabupaten = (data) => {

  return (dispatch) => {
    loadingStatus(false);
    http
      .post(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/EditKabupaten`,
        data
      )
      .then((res) => {
        if(res.data.isSuccess){
            dispatch({ type: "MODAL_KAB", payload: false });
            dispatch(GetKabupatens("GET_KABUPATENS", data.IdProvinsi));
        }else{
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }

      });
  }
}

export const DeleteKabupaten = (idKabupaten, namaKab, idProv) =>{
  console.log(idProv)
  return(dispatch) =>{
      Swal.fire({
        title: "Apakah anda yakin?",
        text: `Akan menghapus Kabupaten ${namaKab}`,
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Ya, Akan Menghapus",
      }).then((result) => {
        if (result.isConfirmed) {
          http
            .post(
              `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/DeleteKabupaten?IdKabupaten=${idKabupaten}`
            )
            .then((res) => {
              let result = res.data;
              if (result.isSuccess) {
                Swal.fire(`Sukses`,`Data ${namaKab} berhasil dihapus`,`success`);
                 dispatch({ type: "RESET_FORM_KAB" });
                 dispatch(GetKabupatens("GET_KABUPATENS", idProv));
              } else {
                Swal.fire("Gagal", `${result.returnMessage}`, "error");
              }
            });

        }
      });
  }
}


