
import {http} from '../../helper/http';
import Swal from 'sweetalert2';
import { useHistory } from "react-router-dom";
export const setFormLogin = (formType, formValue) => {
  console.log(formValue);
    return {type: 'login', formType, formValue}
}

export const apiLogin = (iData) => {

    return (dispatch) => {
          http.get(`${process.env.REACT_APP_ROOT_API}UserApps/Login?UsernameOrEmail=${iData.UsernameOrEmail}&Password=${iData.Password}`

        ).then((res) => {

            let data = res.data;
            if (data.isSuccess) {
                sessionStorage.setItem('isSuccessLgn', true);
                sessionStorage.setItem('idUser', res.data.data.idUser);
                Swal.fire('Berhasil', `Selamat datang!!!`, 'success').then(() => window.location = '/')
            } else {
                Swal.fire('Gagal', `${data.returnMessage}`, 'error');
            }
        })
    }
}
