
import Swal from 'sweetalert2'
export const loadingStatus = (bool = true) => {
  if (bool) {
    Swal.fire({
      title: "proses sedangan dilakukan",
      html: "silahkan menunggu...",
      showConfirmButton: false,
      onBeforeOpen() {
        Swal.showLoading();
      },
      onAfterClose() {
        Swal.hideLoading();
      },
      allowOutsideClick: false,
      allowEscapeKey: false,
      allowEnterKey: false,
    });
  } else {
    Swal.close();
  }
};
