import React,{useState, useEffect} from 'react';

// import Core UI
import {
  CCard,
  CCol,
  CRow,
  CCardHeader,
  CCardBody,
  CButton,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CForm,
  CFormGroup,
  CLabel,
  CInput,
  CSelect
} from '@coreui/react';
import MaterialTable from 'material-table';
import { useDispatch, useSelector } from 'react-redux';

import CIcon from '@coreui/icons-react'
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {
  setDataComboProv,
  GetKabupatens,
  GetKabupatenById,
  setFormKab,
  PostKabupaten,
  UpdateKabupaten,
  DeleteKabupaten,
} from "src/common/redux/action";


const Kabupaten = () => {


    const [stateUpdate, setStateUpdate] = useState(false);
    const [changeIdProv, setChangeIdProv] = useState(0);
    const { dataComboProv } = useSelector((state) => state.dropdownReducer);
    const { GetKabupaten, formKabupaten, modalKab } = useSelector(
      (state) => state.kabupatenReducer
    );
      console.log(GetKabupaten);
    const {
      IdProvinsi,
        Kode,
        Nama
    } = formKabupaten;

     const showModal = () =>
       dispatch({ type: "MODAL_KAB", payload: !modalKab });

    const prosesKabAdd = () => dispatch(PostKabupaten(formKabupaten));

    const prosesKabEdit = () => dispatch(UpdateKabupaten(formKabupaten));

    const prosesDelete = (idKab, NamaKab, idProv) =>
      dispatch(DeleteKabupaten(idKab, NamaKab, idProv));

      const setChangeProv = (formType, formValue) => {
         const changeIdProvNew = {...changeIdProv};
         setChangeIdProv(formValue);
      };

    const getProv = () => dispatch(setDataComboProv("DATA_CMB_PROV"));

    const dispatch = useDispatch();

    const handleModalKabupaten = (type, idKab)=>{

      if (type == "add_kab"){
        showModal();
        setStateUpdate(false);
        dispatch({type:"RESET_FORM_KAB"})
      }else{
         setStateUpdate(true);
         dispatch(GetKabupatenById(idKab));
      }
    }

    const filterKabupatens = () => {
      return dispatch(GetKabupatens("GET_KABUPATENS", changeIdProv));

    }
    const handleEditKab = () => {};

    const columns = [
      {
        title: "Aksi",
        field: "internal_action",
        cellStyle: {
          backgroundColor: "#039be5",
          color: "#FFF",
        },
        headerStyle: {
          backgroundColor: "#039be5",
        },
        render:( rowData) => {
            return (
              <div className="row">
                <button
                  type="button"
                  className="btn btn-sm btn-dark mr-2 ml-4"
                  onClick={() =>
                    handleModalKabupaten("edit", rowData.idKabupaten)
                  }
                >
                  <CIcon name="cil-pencil" /> Update
                </button>

                <button
                  type="button"
                  className="btn btn-sm btn-danger"
                  onClick={() =>
                    prosesDelete(
                      rowData.idKabupaten,
                      rowData.nama,
                      rowData.idProvinsi
                    )
                  }
                >
                  <CIcon name="cil-drop" /> Hapus
                </button>
              </div>
            );
        }
      },
      { title: "Kode", field: "kode" },
      { title: "Nama Kabupaten", field: "nama" },
    ];

    useEffect(() => {
      getProv();
    }, []);

    return (
      <CRow>
        <CCol md="12" xl="12" xs="12">
          <CCard>
            <CCardHeader>
              <CCol md="3">
                <CButton
                  block
                  color="primary"
                  onClick={showModal}
                >
                  <FontAwesomeIcon icon="plus" />
                  Tambah
                </CButton>
              </CCol>
            </CCardHeader>
            <CCardBody>
              <CCol md="12">
                <CRow>
                  <CCol md="2" className="pt-2">
                    <CLabel htmlFor="filter_provinsi">Provinsi</CLabel>
                  </CCol>
                  <CCol md="3" className="mb-2">
                    <CSelect
                      custom
                      name="filter_provinsi"
                      id="filter_provinsi"
                      onChange={(event) =>
                        setChangeProv("IdProvinsi", event.target.value)
                      }
                    >
                      <option value="0"> --Pilih Provinsi -- </option>
                      {dataComboProv.map((v) => (
                        <option key={v.idProvinsi} value={v.idProvinsi}>
                          {v.nama}
                        </option>
                      ))}
                    </CSelect>
                  </CCol>
                  <CCol md="3">
                    <CButton
                      className="btn btn-info"
                      onClick={filterKabupatens}
                    >
                      <FontAwesomeIcon icon="search" /> Filter
                    </CButton>
                  </CCol>
                </CRow>
              </CCol>
              <MaterialTable
                title=""
                options={{
                  headerStyle: {
                    backgroundColor: "#01579b",
                    color: "#FFF",
                  },
                  filtering: true,
                }}
                columns={columns}
                data={GetKabupaten}
              />
            </CCardBody>
          </CCard>
        </CCol>

        <CModal
          show={modalKab}
          onClose={showModal}
          size="lg"
        >
          <CModalHeader closeButton>
            <CModalTitle>Form Kabupaten</CModalTitle>
          </CModalHeader>
          <CModalBody>
            <CForm>
              <CFormGroup>
                <CLabel htmlFor="nf-idProv">Pilih Provinsi</CLabel>
                <CSelect
                  custom
                  name="filter_provinsi_modal"
                  id="filter_provinsi_modal"
                  value={IdProvinsi}
                  onChange={(e) =>
                    dispatch(setFormKab("IdProvinsi", e.target.value))
                  }
                >
                  <option value="0"> --Pilih Provinsi -- </option>
                  {dataComboProv.map((v) => (
                    <option key={v.idProvinsi} value={v.idProvinsi}>
                      {v.nama}
                    </option>
                  ))}
                </CSelect>
              </CFormGroup>
              <CFormGroup>
                <CLabel htmlFor="nf-kodeProv">Kode Kabupaten</CLabel>
                <CInput
                  type="text"
                  id="nf-kode"
                  name="Kode"
                  placeholder="Input Kode.."
                  value={Kode}
                  maxLength="4"
                  onChange={(e) => dispatch(setFormKab("Kode", e.target.value))}
                />
              </CFormGroup>
              <CFormGroup>
                <CLabel htmlFor="nf-nama"> Nama Kabupaten</CLabel>
                <CInput
                  type="text"
                  id="nf-nama"
                  name="Nama"
                  placeholder="Input Nama Provinsi .."
                  value={Nama}
                  onChange={(e) => dispatch(setFormKab("Nama", e.target.value))}
                ></CInput>
              </CFormGroup>
            </CForm>
          </CModalBody>
          <CModalFooter>
            <CButton color="primary" onClick={ stateUpdate ? prosesKabEdit : prosesKabAdd}>
              {stateUpdate ? 'Update' : 'Tambah'}
            </CButton>
            <CButton color="secondary" onClick={showModal}>
              Cancel
            </CButton>
          </CModalFooter>
        </CModal>
      </CRow>
    );
}

export default Kabupaten;
