
import { http } from "src/common/";
import serviceName from "src/common/helper/serviceName";
import Swal from "sweetalert2";
import { loadingStatus } from "./commonAction";

/**
 * set change input modal kecukupan
 * @param {*} type
 * @returns
 */
export const setFormKecukupanAction = (formType, formValue) => {
  return { type: "ADD_KECUKUPAN", formType, formValue };
};

/**
 * set change input modal ketersediaan
 * @param {*} type
 * @returns
 */

export const setFormKetersediaanAction = (formType, formValue) => {
  return { type: "ADD_KETERSEDIAAN", formType, formValue };
};

/**
 * set change input modal ketersediaan
 * @param {*} type
 * @returns
 */

export const setFormKondisiAction = (formType, formValue) => {
  return { type: "ADD_KONDISI", formType, formValue };
};


export const GetBobotKecukupan = (type) => {
  return (dispatch) => {
    loadingStatus(true);
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcBobot}/GetBobotKecukupans`
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          dispatch({ type, payload: data.data });
        } else {
          Swal.fire("Gagal", `${data.ReturnMessage}`, "error");
        }
      });
  };
};
