export const PostBobotKecukupanAction = (data) =>{
  let idUser = sessionStorage.getItem("idUser");
  return(dispatch)=>{
    loadingStatus(true);
    let param = `?IdUser=${idUser}&Nama=${data.Nama}&Bobot=${data.Bobot}`;
    http
      .post(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcBobot}/AddBobotKecukupan${param}`
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          Swal.fire("Berhasil", `Anda telah berhasil menambah data`, "success");
          dispatch({ type: "MODAL_KECUKUPAN", payload: false });
          dispatch({ type: "RESET_FORM_KECUKUPAN" });
          dispatch(GetBobotKecukupan("GET_LIST_KECUKUPAN"));
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  }
}

