export const SetBobokKecukupanByIdAction = (id) =>{
  return (dispatch) => {
    loadingStatus(true);
    dispatch({ type: "RESET_FORM_KAB" });
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcBobot}/GetBobotKecukupan?Id=${id}`
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          dispatch(setFormKecukupanAction("Id", id));
          dispatch(setFormKecukupanAction("Nama", data.data.nama));
          dispatch(setFormKecukupanAction("Bobot", data.data.bobot));
          dispatch({ type: "MODAL_KECUKUPAN", payload: true });
        } else {
          Swal.fire("Gagal", `${data.ReturnMessage}`, "error");
        }
      });
  };
}

