export const UpdateBobotKecukupanAction = (data) => {
  let idUser = sessionStorage.getItem("idUser");
  return (dispatch) => {
    loadingStatus(false);
    http
      .post(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcBobot}/EditBobotKecukupan?IdUser=${idUser}`,
        data
      )
      .then((res) => {
        if (res.data.isSuccess) {
          dispatch({ type: "MODAL_KECUKUPAN", payload: false });
         dispatch(GetBobotKecukupan("GET_LIST_KECUKUPAN"));
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
};

