export const DeleteKecukupanAction = (id, nama) => {
  let idUser = sessionStorage.getItem("idUser");
  return (dispatch) => {
    Swal.fire({
      title: "Apakah anda yakin?",
      text: `Akan menghapus Bobot ${nama}`,
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Ya, Akan Menghapus",
    }).then((result) => {
      if (result.isConfirmed) {
        http
          .post(
            `${process.env.REACT_APP_ROOT_API}${serviceName.svcBobot}/DeleteBobotKecukupan?IdUser=${idUser}&Id=${id}`
          )
          .then((res) => {
            let result = res.data;
            if (result.isSuccess) {
              Swal.fire(`Sukses`, `Data ${nama} berhasil dihapus`, `success`);
              dispatch({ type: "RESET_FORM_KECUKUPAN" });
               dispatch(GetBobotKecukupan("GET_LIST_KECUKUPAN"));
            } else {
              Swal.fire("Gagal", `${result.returnMessage}`, "error");
            }
          });
      }
    });
  };
};

