import { http } from 'src/common/'
import serviceName from 'src/common/helper/serviceName';
import Swal from 'sweetalert2';
import { loadingStatus } from './commonAction';



export const setFormVariabel = (formType, formValue) => {
  return { type: "ADD_VARIABEL", formType, formValue };
};

/**
 * GetVariabel
 * @param {*} data
 * @returns : LIST Variabel
 */

export const GetDataVariabel = (type) => {
  return (dispatch) => {
    loadingStatus(true);
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterVariabel}/GetVariabels`
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          dispatch({ type, payload: data.data });
        } else {
          Swal.fire("Gagal", `${data.ReturnMessage}`, "error");
        }
      });
  };
}

/**
 *
 * @param {idVar} data
 * @returns
 */

export const setVariabelId = (idVariabel) =>{
 return (dispatch) => {
   loadingStatus(true);
   dispatch({ type: "RESET_FORM_VARIABEL" });
   http
     .get(
       `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterVariabel}/GetVariabel?IdVariabel=${idVariabel}`
     )
     .then((res) => {
       loadingStatus(false);
       let data = res.data;
       if (data.isSuccess) {
         dispatch(setFormVariabel("IdVariabel", idVariabel));
         dispatch(
           setFormVariabel("IdJenisVariabel", data.data.idJenisVariabel)
         );
         dispatch(setFormVariabel("Nama", data.data.nama));
         dispatch(setFormVariabel("Presentase", data.data.presentase));
         dispatch({ type: "MODAL_VARIABEL", payload: true });
       } else {
         Swal.fire("Gagal", `${data.ReturnMessage}`, "error");
       }
     });
 };
}

/**
 * add new variabel
 */

export const PostVariabel = (data) => {
  return (dispatch) => {
    loadingStatus(true);
    let idUser = sessionStorage.getItem("idUser");
    let param = `?IdUser=${idUser}&IdJenisVariabel=${data.IdJenisVariabel}&Nama=${data.Nama}&Presentase=${data.Presentase}`;
    http
      .post(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterVariabel}/AddVariabel${param}`
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          Swal.fire("Berhasil", `Anda telah berhasil menambah data`, "success");
          dispatch({ type: "MODAL_VARIABEL", payload: false });
          dispatch({ type: "RESET_FORM_VARIABEL" });
            dispatch(GetDataVariabel("GET_VARIABELS"));
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
};

/***
 * Proses udpate
 */
export const UpdateVariabel = (data) => {
  let idUser = sessionStorage.getItem('idUser');
  return (dispatch) => {
    loadingStatus(false);
    http
      .post(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterVariabel}/EditVariabel?IdUser=${idUser}`,
        data
      )
      .then((res) => {
        if (res.data.isSuccess) {
          dispatch({ type: "MODAL_VARIABEL", payload: false });
          dispatch(GetDataVariabel("GET_VARIABELS"));
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
}

export const DeleteVariabel = (idVariabel, nama) => {
  let idUser = sessionStorage.getItem('idUser');
  return (dispatch) => {
    Swal.fire({
      title: "Apakah anda yakin?",
      text: `Akan menghapus variabel ${nama}`,
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Ya, Akan Menghapus",
    }).then((result) => {
      if (result.isConfirmed) {
        http
          .post(
            `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterVariabel}/DeleteVariabel?IdUser=${idUser}&IdVariabel=${idVariabel}`
          )
          .then((res) => {
            let result = res.data;
            if (result.isSuccess) {
              Swal.fire(
                `Sukses`,
                `Data ${nama} berhasil dihapus`,
                `success`
              );
              dispatch({ type: "RESET_FORM_VARIABEL" });
              dispatch(GetDataVariabel("GET_VARIABELS"));
            } else {
              Swal.fire("Gagal", `${result.returnMessage}`, "error");
            }
          });
      }
    });
  };
};

