import { http } from "src/common/";
import serviceName from "src/common/helper/serviceName";
import Swal from "sweetalert2";
import { loadingStatus } from "./commonAction";


export const setFormJabatanAction = (formType, formValue) => {
  return { type: "ADD_JABATAN", formType, formValue };
};

export const GetDataJabatanAction = (type) => {
  return (dispatch) => {
    loadingStatus(true);
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/GetJabatans`
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          dispatch({ type, payload: data.data });
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
};

export const PostJabatanAction = (data) => {
  return (dispatch) => {
    loadingStatus(true);
    let idUser = sessionStorage.getItem("idUser");
    let param = `?Kode=${data.Kode}&Nama=${data.Nama}&Presentase=${data.Presentase}`;
    http
      .post(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/AddJabatan${param}`
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          Swal.fire("Berhasil", `Anda telah berhasil menambah data`, "success");
          dispatch({ type: "MODAL_JABATAN", payload: false });
          dispatch({ type: "RESET_FORM_JABATAN" });
         dispatch(GetDataJabatanAction("GET_LIST_JABATAN"));
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
};


export const setJabatanIdAction = (idJabatan) => {
  return (dispatch) => {
    loadingStatus(true);
    dispatch({ type: "RESET_FORM_JABATAN" });
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/GetJabatan?IdJabatan=${idJabatan}`
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          dispatch(setFormJabatanAction("IdJabatan", idJabatan));
          dispatch(
            setFormJabatanAction("Kode", data.data.kode)
          );
          dispatch(setFormJabatanAction("Nama", data.data.nama));
          dispatch({ type: "MODAL_JABATAN", payload: true });
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
};

/**
 *
 * @param {*} json (data)
 * @returns
 */
export const UpdateJabatanAction = (data) => {
  return (dispatch) => {
    loadingStatus(false);
    http
      .post(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/EditJabatan`,
        data
      )
      .then((res) => {
        if (res.data.isSuccess) {
          dispatch({ type: "MODAL_JABATAN", payload: false });
          dispatch({ type: "RESET_FORM_JABATAN" });
          dispatch(GetDataJabatanAction("GET_LIST_JABATAN"));
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
}


export const DeleteJabatanAction = (id, nama) => {
  let idUser = sessionStorage.getItem("idUser");
  return (dispatch) => {
    Swal.fire({
      title: "Apakah anda yakin?",
      text: `Akan menghapus Jabatan ${nama}`,
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Ya, Akan Menghapus",
    }).then((result) => {
      if (result.isConfirmed) {
        http
          .post(
            `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/DeleteJabatan?IdJabatan=${id}`
          )
          .then((res) => {
            let result = res.data;
            if (result.isSuccess) {
              Swal.fire(`Sukses`, `Data ${nama} berhasil dihapus`, `success`);
               dispatch({ type: "MODAL_JABATAN", payload: false });
               dispatch({ type: "RESET_FORM_JABATAN" });
               dispatch(GetDataJabatanAction("GET_LIST_JABATAN"));
            } else {
              Swal.fire("Gagal", `${result.returnMessage}`, "error");
            }
          });
      }
    });
  };
};
