
import { http } from "src/common/helper";
import serviceName from "src/common/helper/serviceName";
import Swal from "sweetalert2";
import { loadingStatus } from "./commonAction";



export const setDataComboProv = (type) => {
  return (dispatch) => {
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/GetProvinsis`
      )
      .then((res) => {
        let data = res.data;
        if (data.isSuccess) {
          dispatch({ type, payload: data.data });
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
};

export const SetJenisVariabel = (type) => {
  return (dispatch) => {
    loadingStatus(false)
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterVariabel}/GetJenisVariabel`
      )
      .then((res) => {
        loadingStatus(false);
        let result = res.data;
        if (result.isSuccess) {
          dispatch({type,payload:result.data})
        }else{
           Swal.fire("Gagal", `${result.returnMessage}`, "error");
        }
      });
  };
};
